/*Сброс стилей*/
.page {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  overflow-x: hidden;
  position: relative;
}
ul {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
}
* {
  box-sizing: border-box;
}
li {
  list-style-type: none;
}
.img {
  max-width: 100%;
  display: block;
  padding: 0;
}
.form__input {
  outline: none;
  -webkit-appearance: none;
  font-size: 13px;
  font-family: 'Ubuntu', sans-serif;
  padding: 0;
  margin: 0;
  background-color: transparent;
}
.form__button {
  outline: none;
  -webkit-appearance: none;
  font-size: 13px;
  font-family: 'Ubuntu', sans-serif;
  padding: 0;
  margin: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
  transition: .3s;
}
/*плейсхолдер*/
::-webkit-input-placeholder {
  color: #ababab;
  opacity: 1;
}
/* webkit */
::-moz-placeholder {
  color: #ababab;
  opacity: 1;
}
/* Firefox 19+ */
:-moz-placeholder {
  color: #ababab;
  opacity: 1;
}
/* Firefox 18- */
:-ms-input-placeholder {
  color: #ababab;
  opacity: 1;
}
/* IE */
:focus::-webkit-input-placeholder {
  opacity: 0;
}
/* webkit */
:focus::-moz-placeholder {
  opacity: 0;
}
/* Firefox 19+ */
:focus:-moz-placeholder {
  opacity: 0;
}
/* Firefox 18- */
:focus:-ms-input-placeholder {
  opacity: 0;
}
/* IE */
.page {
  background-color: #fffbf1;
}
.full-screen::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  margin: auto;
  left: -50vw;
  right: -50vw;
  z-index: -2;
}
.full-screen-left::after {
  content: '';
  display: block;
  height: 100%;
  width: 50vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  margin: auto;
  left: -50vw;
  z-index: -2;
}
.item {
  display: flex;
  align-items: center;
  justify-content: center;
}
.link {
  text-decoration: none;
  font-size: 14px;
  cursor: pointer;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 300;
  align-self: center;
  margin-right: 18px;
  transition: .3s;
}
.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
}
.img {
  max-width: 100%;
}
.figure {
