}
.copyright {
  display: flex;
  height: 50px;
  background-color: #222121;
  margin: 0 -30px;
  align-items: center;
  position: relative;
}
.copyright::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  margin: auto;
  left: -50vw;
  right: -50vw;
  z-index: -1;
}
.copyright__text {
  font-family: Ubuntu, sans-serif;
  font-weight: 300;
  padding-left: 30px;
  color: #c0c0c0;
