@media (max-width: 990px) {
  .header-main__connect {
    flex-direction: column;
  }
  .header-main-connect__tel,
  .header-main-connect__email {
    margin-top: 10px;
  }
  .form__search {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    padding-bottom: 30px;
  }
  .basket {
    margin-bottom: 30px;
  }
  .main__gray-back {
    height: 10px;
  }
  .aside__baner {
    display: none;
  }
  .aside {
    width: 10%;
  }
  .submenu-tab {
    display: none;
  }
  .aside__item {
    border-bottom: 0;
  }
  .aside__title {
    display: none;
  }
  .menu__ul {
    margin-top: 40px;
  }
  .main {
    width: 90%;
  }
}
@media (max-width: 800px) {
  .main__content {
    flex-direction: column-reverse;
  }
  .main {
    width: 100%;
  }
  .main__slider {
    margin: 0 -30px 0 -30px;
    width: auto;
    margin-bottom: 20px;
  }
  .main__gray-back {
    display: none;
  }
  .aside__menu {
    margin-bottom: 15px;
  }
  .arrow__left {
    display: none;
  }
  .arrow__right {
    display: none;
  }
  .menu__ul {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
  }
  .icon_piano,
  .icon_trump,
  .icon_violin,
  .icon_drum,
  .icon_sheet_music,
  .icon_accessories {
    font-size: 27px;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .main-logo {
    width: 230px;
  }
  .header-main__logo {
    width: auto;
    margin-left: 0;
    flex-grow: 0;
  }
  .footer-logo__pict {
    width: 100%;
  }
  .header-main-center__content {
    flex-direction: column;
    justify-content: center;
  }
  .header-main__search {
    align-items: flex-end;
    align-self: flex-end;
    width: auto;
  }
  .header-main__connect {
    flex-direction: column;
    align-self: flex-end;
  }
  .header-main-connect__tel,
  .header-main-connect__email {
    margin-top: 10px;
    margin-right: 0;
    flex-direction: row-reverse;
    align-items: center;
    align-self: flex-end;
  }
  .form__search {
    width: 185px;
    padding-top: 10px;
    padding-bottom: 20px;
    align-self: flex-end;
  }
  .basket {
    margin-left: 0;
  }
  .header-main {
    flex-wrap: wrap;
  }
}
@media (max-width: 550px) {
  .header__main-menu {
    align-items: flex-start;
    flex-direction: column;
    position: relative;
  }
  .menu__main {
    display: none;
    position: absolute;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px 0 30px 30px;
    top: 45px;
    width: 100%;
    z-index: 2;
  }
  .hide__menu {
    display: flex;
    align-self: flex-start;
  }
}
@media (max-width: 500px) {
  .header__main {
    flex-direction: column;
    align-items: center;
  }
  .header-main__logo {
    width: auto;
    margin-left: 0;
    margin-right: 0;
    flex-grow: 0;
    padding-top: 40px;
  }
  .span {
    display: none;
  }
}
